.reviews {
  display: flex;
  flex-direction: column;
  margin-bottom: 105px;
}

.reviews__title {
  font-size: 30px;
  line-height: 40px;
  font-style: normal;
  font-weight: bold;
  padding-right: 21px;
  padding-bottom: 33px;
  text-align: center;
  font-weight: bold;
}
.reviews__button {
  width: 50px;
  height: 50px;
  border: 1px solid $light-blue-color;
  border-radius: 50%;
  background-color: $light-blue-color;
  top: 47%
}
.reviews__arrows {
  background-image: url(/source/img/arrow-up-icon.svg);
  background-size: 150%;
}
.reviews__arrows_prev {
  transform: rotate(-90deg);
}
.reviews__arrows_next {
  transform: rotate(90deg);
}
